
  
  const validator = store => next => action => {
      const isNote = action.type && action.type.startsWith('Note');
      if (isNote) {
        try {
          const note = action.payload;
          const notValid = !note.budget || !note.name;
          if (notValid) {
            throw new Error('Your note must include a title and some content');
          } else {
            return next(action);
          }
        } catch (err) {
          alert(err);
      }
    } else {
      return next(action);
    }
  }
  
  export default validator;



